import Vue from 'vue'
import App from './App'

Vue.config.productionTip = false
App.mpType = 'app'

const app = new Vue(App)
app.$mount()

export default {
  // 这个字段走 app.json
  config: {
    pages: [
      'pages/logs/logs',
      'pages/index/index',
      'pages/person/person',
      'pages/rename/rename',
      'pages/integral/integral',
      'pages/address/address',
      '^pages/addressmenu/addressmenu',
      'pages/integdetail/integdetail',
      'pages/addressdetail/addressdetail'
    ], // 页面前带有 ^ 符号的，会被编译成首页，其他页面可以选填，我们会自动把 webpack entry 里面的入口页面加进去
    window: {
      backgroundTextStyle: 'light',
      navigationBarBackgroundColor: '#63B8FF',
      navigationBarTitleText: 'WeChat',
      navigationBarTextStyle: 'black'
    },
    tabBar: {
      color: '#000',
      selectedColor: '#63B8FF',
      backgroundColor: '#ffffff',
      borderStyle: 'black',
      list: [
        {
          iconPath: 'pages/images/home.png',
          selectedIconPath: 'pages/images/home-selected.png',
          pagePath: 'pages/index/index',
          text: '首页'
        },
        {
          iconPath: 'pages/images/integral.png',
          selectedIconPath: 'pages/images/integral-selected.png',
          pagePath: 'pages/logs/logs',
          text: '商城'
        },
        {
          iconPath: 'pages/images/personal.png',
          selectedIconPath: 'pages/images/personal-selected.png',
          pagePath: 'pages/person/person',
          text: '我的'
        }
      ]
    }
  }
}



